@import url(https://fonts.googleapis.com/css?family=Exo:300,700);
html {
  box-sizing: border-box;
  font-size: 100%;
}

*,
*:before,
*:after {
  box-sizing: inherit;
}

body,
h1,
h2,
h3,
h4,
h5,
h6,
p,
ol,
ul {
  margin: 0;
  padding: 0;
  font-weight: normal;
  font-family: "Exo", sans-serif;
}

ol,
ul {
  list-style: none;
}

img {
  max-width: 100%;
  height: auto;
}

input {
  outline: none;
}

body {
  background-color: #1167fa; }

.container-outer {
  height: 100vh;
  text-align: center;
  position: relative; }

.container-center {
  width: 100%;
  position: absolute;
  left: 50%;
  top: 50%;
  -webkit-transform: translate(-50%, -50%);
          transform: translate(-50%, -50%); }

.sector-sm {
  max-width: 300px;
  margin: auto; }

.sector-md {
  max-width: 750px;
  margin: auto; }

.sector-lg {
  max-width: 1200px;
  margin: auto; }

h1 {
  font-size: 3rem;
  font-weight: bold;
  color: #fed546;
  margin-bottom: 3rem; }

button,
.label-hero-name {
  display: inline-block;
  padding: 0 1.5rem;
  font-size: 1.8rem;
  line-height: 3rem;
  background-color: #fef382;
  color: #f2d024;
  border-radius: 5px;
  border-left: 5px solid #f2d024;
  border-bottom: 5px solid #f2d024;
  border-right: 5px solid #fbf6d3;
  border-top: 5px solid #fbf6d3;
  margin: 3rem 0.5rem 0 0.5rem; }

.container-input {
  position: relative;
  line-height: 3rem;
  font-size: 1.5rem;
  color: #cddffe; }
  .container-input input {
    color: inherit;
    line-height: inherit;
    font-size: inherit;
    width: 100%;
    padding-left: 1rem;
    background-color: rgba(0, 0, 0, 0);
    border: none;
    border-bottom: 2px solid #cddffe; }
    .container-input input::-webkit-input-placeholder {
      color: #cddffe; }
    .container-input input::-moz-placeholder {
      color: #cddffe; }
    .container-input input:-ms-input-placeholder {
      color: #cddffe; }
    .container-input input::-ms-input-placeholder {
      color: #cddffe; }
    .container-input input::placeholder {
      color: #cddffe; }
  .container-input .btn-arrow {
    display: inline-block;
    position: absolute;
    right: 0;
    top: 0; }
    .container-input .btn-arrow:hover {
      cursor: pointer; }

.container-mask {
  position: relative; }

.hero-mask {
  width: 90%;
  position: absolute;
  left: 50%;
  bottom: 50%;
  -webkit-transform: translateX(-50%);
          transform: translateX(-50%); }

.btn-pre,
.btn-next {
  position: absolute;
  font-size: 4rem;
  top: 50%;
  color: #fed546; }
  .btn-pre:hover,
  .btn-next:hover {
    cursor: pointer; }

.btn-pre {
  left: -25%; }

.btn-next {
  right: -25%; }

@-webkit-keyframes showSuperpower {
  from {
    top: 100%;
    opacity: 1; }
  to {
    top: 0%;
    opacity: 0; } }

@keyframes showSuperpower {
  from {
    top: 100%;
    opacity: 1; }
  to {
    top: 0%;
    opacity: 0; } }

.superpower {
  width: 100%;
  position: absolute;
  top: 100%;
  opacity: 1;
  -webkit-animation-name: showSuperpower;
          animation-name: showSuperpower;
  -webkit-animation-duration: 1s;
          animation-duration: 1s;
  -webkit-animation-iteration-count: infinite;
          animation-iteration-count: infinite; }

.container-colour {
  position: absolute;
  right: -40%;
  top: 0;
  -webkit-transform: translateY(50%);
          transform: translateY(50%); }

